import DeepData from "../../../assets/js/formate.js";
export default {
  data() {
    return {
      orderlist: ["ascending", "descending"],
      tableData: [],
      total: 0,
      rules: {
        title: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        info: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        inscribe: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        notice_time: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        start_time: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        end_time: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ]
      },
      form: {
        title: "",
        info: "",
        inscribe: "",
        notice_time: "",
        start_time: "",
        end_time: "3",
        redactor: ""
      },

      currentPage: 1,
      limit: 10,
      visible: false,
      title: "添加停服公告",
      selectList: []
    };
  },
  created() {
    this.initdata({ page: this.currentPage, limit: this.limit });
  },
  methods: {
    // 批量删除
    del() {
      //勾选需要删除的项目批量删除
      if (this.selectList.length != 0) {
        //###1.删除dom的数据
        //2.删除后台的数据
        console.log("已经有数据了");
        this.$confirm("确认删除吗?", "信息", {
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        })
          .then(async () => {
            let str = this.selectList.join();
            // console.log(str);
            let { data } = await this.$http.HallFunConfig.DeleteStopNotice({
              id_list: `(${str})`
            });
            console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.initdata({
                page: this.currentPage,
                limit: this.limit,
                title: this.searchinput
              });
            }

            this.$message({
              type: "success",
              message: "删除成功!"
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      } else {
        this.$message("请选择需要删除的数据");
      }
    },

    //添加
    add() {
      this.editForm("添加停服公告", true, {});
    },

    //表格选中
    handleSelectionChange(sel) {
      let idList = sel.map(item => item.id);
      // console.log(idList);

      this.selectList = idList;
    },

    //页容量改变
    handleSizeChange(num) {
      this.limit = num;
      this.currentPage = 1;
      this.initdata({ page: this.currentPage, limit: this.limit });
    },

    //页码改变
    handleCurrentChange(pagenum) {
      this.currentPage = pagenum;
      this.initdata({ page: this.currentPage, limit: this.limit });
    },

    //表格编辑
    handleEdit(row) {
      console.log(row);
      let formData = DeepData(row);
      formData.start_time = this.data(row.start_time);
      formData.end_time = this.data(row.end_time);
      formData.notice_time = this.data(row.notice_time);

      this.editForm("更新停服公告", true, formData);
    },

    //表格删除
    handleDelete(x, row) {
      // console.log(x,row.id);

      this.$confirm("确认删除吗?", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(async () => {
          // console.log(str);
          let { data } = await this.$http.HallFunConfig.DeleteStopNotice({
            id: row.id
          });
          // console.log(data);
          if (data.code === 1 && data.msg === "ok") {
            this.initdata({
              page: this.currentPage,
              limit: this.limit
            });
          }

          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    //表单提交
    onSubmit(formName, type) {
      //表单验证
      this.$refs[formName].validate(async valid => {
        if (valid) {
          this.form.start_time = Math.ceil(this.form.start_time / 1000);
          this.form.end_time = Math.ceil(this.form.end_time / 1000);
          this.form.notice_time = Math.ceil(this.form.notice_time / 1000);
          if (type === "添加停服公告") {
            console.log(this.form);

            this.form.redactor = "redactor";
            let { data } = await this.$http.HallFunConfig.PostStopNotice(
              this.form
            );
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.initdata({ page: this.currentPage, limit: this.limit });
              this.$message({
                type: "success",
                message: "保存成功!"
              });
            }else{
               this.$message({
                type: "warning",
                message: "保存失败"
              });
            }
          } else if (type === "更新停服公告") {
            console.log(this.form);

            let { data } = await this.$http.HallFunConfig.PutStopNotice(
              this.form
            );
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.initdata({ page: this.currentPage, limit: this.limit });
  
              this.loading = false;
              this.$message({
                type: "success",
                message: "编辑成功!"
              });
            } else {
              this.loading = false;
              this.$message({
                type: "warning",
                message: "编辑失败"
              });
            }
          }

          this.editForm("添加停服公告", false, {});
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    //表单返回
    back() {
      this.editForm("添加停服公告", false, {});
    },

    editForm(title, visible, form) {
      this.title = title;
      this.visible = visible;
      this.form = form;
    },

    //获取时间格式
    initTime(today) {
      let myDate = new Date(today);
      let year = myDate.getFullYear();
      let month =
        myDate.getMonth() + 1 < 10
          ? "0" + (myDate.getMonth() + 1)
          : myDate.getMonth() + 1;
      let day =
        myDate.getDate() < 10 ? "0" + myDate.getDate() : myDate.getDate();
      let hours =
        myDate.getHours() < 10 ? "0" + myDate.getHours() : myDate.getHours();
      let minutes =
        myDate.getMinutes() < 10
          ? "0" + myDate.getMinutes()
          : myDate.getMinutes();
      let seconds =
        myDate.getSeconds() < 10
          ? "0" + myDate.getSeconds()
          : myDate.getSeconds();
      return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    },

    data(time) {
      let long1 = Date.parse(time);
      let long2 = new Date(long1).getTime();
      return long2;
    },

    async initdata(params) {
      let { data } = await this.$http.HallFunConfig.GetStopNotice(params);
      this.tableData = data.data;
      this.total = data.total;
      this.tableData.forEach(item => {
        item.start_time = this.initTime(item.start_time * 1000);
        item.end_time = this.initTime(item.end_time * 1000);
        item.notice_time = this.initTime(item.notice_time * 1000);
      });
      // console.log(localdata);
      console.log(data.data);
    }
  }
};



